var url = require('url');

export default {
    getEndpoints(type) {
        if(!_endpoints[type]) return [];
        var currentPoints =  _endpoints[type];
        currentPoints.forEach((point) => {
            var ps = url.parse(point.ssl_endpoint);
            point.host = ps.host;
            point.post = ps.port;
        })
        return _endpoints[type];
    },
    getNetWorkList(){
        return networkList;
    }
}